/**
 * Gets the global version if we're in the browser, else loads the node-fetch module.
 *
 * @internal
 */
export const getFetch = async (): Promise<typeof fetch> => {
  return (globalThis as any).fetch || (await import('cross-fetch')).fetch;
};
